$color: #FAE5E5;

$easing: cubic-bezier(.5, 0, .2, 1);

[data-state="Bad"] {
  --duration: 1000ms;
  --delay: 400ms;
  --color: #FAE5E5;
  --faded-color: #{rgba(#EE9F9F, .5)};
}
[data-state="Good"] {
  --duration: 400ms;
  --delay: 100ms;
  --color: #E7EEF8;
  --faded-color: #{rgba(darken(#E7EEF8,20%), .5)};
}

.list {
  width: 100%;
  height: 50vh;
  // padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  --duration: 400ms;
  --delay: 100ms;
  --color: #E7EEF8;
  --faded-color: #{rgba(darken(#E7EEF8,20%), .5)};
}

.state {
  font-size: 1.5rem;
  margin: 0 0 1rem;
  text-align: center;
}

/* ---------------------------------- */

.device {
  max-width: 100vh;
  display: grid;
  overflow: scroll;

  border-radius: 1vmin;
  background-color: #fff;
  box-shadow: 0 2vmin 3vmin var(--faded-color);

  > * {
    // grid-area: 1 / 1; 
    padding: 1rem;
  }
}


/* ---------------------------------- */

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  color: #000;
  margin: auto;
}

li {
  width: 100vw;
  margin-bottom: .5rem;
  animation: enter var(--duration) $easing backwards;
  display: grid;
  grid-template-columns: 25% auto;
  grid-template-rows: auto auto;
  grid-column-gap: 1rem;
  align-items: center;

  > .title {
    grid-column: 2 / 3;
    grid-row: 1 / 2;
  }

  > p {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
  }

  > .avatar {
    grid-column: 1 / 2;
    grid-row: 1 / -1;
  }

  @for $i from 1 through 5 {
    &:nth-child(#{$i}) {
      animation-delay: calc(#{$i} * var(--delay));
    }
  }
}

@keyframes enter {
  from {
    opacity: 0;
    transform: scale(.7);
  }
}

.content {
  border-radius: .4rem;
  padding: 1rem;
  --bg: var(--color);
  cursor: pointer;
}

.clickable {
  .bg { border: solid 5px var(--faded-color); }
  cursor: pointer;
}

.bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--bg);
  border-radius: inherit;
}

.exit {
  z-index: 1;
  position: absolute;
  top: 1rem;
  right: 1rem;
  padding: 1rem;
  appearance: none;
  background: transparent;
  font: inherit;
  color: white;
  border: none;
  appearance: none;
}

.avatar {
  background-color: var(--faded-color);
  border-radius: .35rem;
  &:before {
    content: '';
    display: block;
    padding-bottom: 100%;
  }
}

p {
  font-size: .65rem;
  line-height: 1.5;
  margin: 0;
  font-weight: bold;
  opacity: 0.3;
  font-family: monospace;
}

.title {
  font-weight: bold;
  opacity: 0.5;
  margin-bottom: .5rem;
  font-family: monospace;
}

/* ---------------------------------- */


.article {
  // background: inherit;

  .avatar { 
    margin-bottom: .5em;
  }
  
  .avatar:before {
    padding-bottom: 40%; 
  }
}

/* ---------------------------------- */
/* Layer Toggling */

[data-layer] { 
  opacity: 0; 
  visibility: hidden;
  transition: opacity var(--duration) linear, visibility 0s linear var(--duration);
}

[data-view="list"] [data-layer="list"],
[data-view="article"] [data-layer="article"] { 
  opacity: 1;
  transition-delay: 0s, 0s;
  visibility: visible; 
}

/* ---------------------------------- */


[data-layer="list"] {
  li { 
    transform: translateY(0%); 
    transition: transform var(--duration) ease; 
  }
}

[data-view="article"] {

  [data-layer="list"] {
    > li { transform: translateY(-100%); }
    .clickable { transform: none; }
    .clickable ~ li { transform: translateY(100%); }
  }
  
  [data-layer="article"] {
    .title, p {
      animation: enter-content calc(var(--duration) * 1.5) calc(var(--delay) / 2) $easing both;
      
      ~ * {
        animation-delay: calc(var(--delay) * 1.5);
      }
    }
  }
}

@keyframes enter-content {
  from {
    transform: translateY(1rem);
    opacity: 0;
  }
}


/* ---------------------------------- */

*, *:before, *:after {
  box-sizing: border-box;
  position: relative;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
}

body, html {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}